@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lilita+One&family=Montserrat&display=swap);
/* Google fonts to import */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.coming-soon-container {
  text-align: center;
}

.devclub-logo {
  height: 40vmin;
  pointer-events: none;
}

.coming-soon-content {
  background-color: white;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.devchamps-background {
    background-size: cover;
    position: fixed;
    left: -5vw;
    top: -5vh;
    height: 110vh;
    width: 110vw;
    z-index: -1;

    object-fit: cover;
    background-position: center;
}

.devchamps-container {
    text-align: center;
}

.devchamps-container > .devchamps-heading {
    width: 95vw;
    max-width: 900px;
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    left: 50%;
    position: relative;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.devchamps-container > .devchamps-heading > .devchamps-title {
    min-width: 0;
    min-height: 0;
    height: 100%;
}

.devchamps-container > .devchamps-heading > .devchamps-ashu {
    min-width: 0;
    min-height: 0;
    height: 100%;
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
}

.devchamps-container > .faq-container {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 800px;
    max-width: 95vw;
    font-family: 'Montserrat';
    margin-bottom: 50px;
}

.devchamps-container > .faq-container .row-title-text {
    margin: 5px;
    margin-left: 20px;
    font-weight: 900;
    color: #5F394B !important;
}

    .devchamps-container > .faq-container .row-title-text:active {
        color: #DB8263 !important;
    }

.devchamps-container > .faq-container .row-content-text {
    margin-left: 15px;
    margin-right: 15px;
    line-height: 1.5rem;
    text-align: left;
}

.devchamps-container > .faq-container .row-content-text em {
    font-weight: 800;
}

.devchamps-container > .hof-coming-soon {
    font-family: 'Bebas Neue';
    color: #EFE7D8;
    font-size: 4rem;
    text-align: center;
}
